<template>
    <singlebox :post="post" :author="author" :stat="stat" v-loading="loading">
        <div class="u-meta u-sub-block" slot="single-header">
            <em class="u-label">Á±ªÂûã</em>
            <span class="u-value">
                {{ post_subtype }}
            </span>
        </div>
        <!-- Ë≠¶Âëä -->
        <el-alert
            v-if="isTool"
            class="m-single-notice"
            title="ÁâπÂà´ËØ¥Êòé"
            type="warning"
            description="ËØ∑‰∏•Ê†º‰øùÊä§‰∏™‰∫∫Ë¥¢‰∫ßÂÆâÂÖ®ÔºåÂãøËΩª‰ø°‰ªª‰ΩïÊî∂Ë¥πÊúçÂä°ÔºåÊú¨Á´ô‰∏çÂØπ‰ΩúËÄÖÊèê‰æõÁöÑ‰∏ãËΩΩËµÑÊ∫êÂÅö‰ªª‰ΩïÊãÖ‰øùÔºåÂá°ÊòØ‰∏çÂèóÂÆòÊñπËÆ§ÂèØ„ÄÅÊàñÁñë‰ººÂ§ñÊåÇÔºå‰ª•Âèä‰∏çÁ¨¶ÂêàÁ´ôÂÜÖÂàõ‰ΩúÂÖ¨Á∫¶ÂèäÂèëÂ∏ÉËßÑËåÉÁöÑ‰ΩúÂìÅ‰∫à‰ª•Âà†Èô§Â§ÑÁêÜÔºåÊ¨¢ËøéÂπøÂ§ßÁé©ÂÆ∂ÁõëÁù£‰∏é‰∏æÊä•„ÄÇ"
            show-icon
        >
        </el-alert>
    </singlebox>
</template>

<script>
import singlebox from "@jx3box/jx3box-page/src/cms-single";
import { getPost } from "../service/post.js";
import { getStat, postStat } from "@jx3box/jx3box-common/js/stat";
import _ from "lodash";
const types = {
    "1" : "Â∑•ÂÖ∑Ê∫êÁ†Å",
    "2" : "ËµÑÊ∫êÂàÜ‰∫´",
    "3" : "Êèí‰ª∂ÊåáÂçó",
    "4" : "Â∏ÆÂä©ÊñáÊ°£"
}

export default {
    name: "single",
    props: [],
    data: function() {
        return {
            loading: false,
            post: {},
            author: {},
            stat: {},
        };
    },
    computed: {
        id: function() {
            return this.$store.state.pid;
        },
        post_subtype: function() {
            let subtype = _.get(this.post, "post_subtype")
            if(subtype){
                return types[subtype]
            }else{
                return '-'
            }
        },
        isTool : function (){
            return this.post.post_subtype == '1'
        }
    },
    methods: {
    },
    filters: {},
    created: function() {
        if (this.id) {
            this.loading = true;
            getPost(this.id, this)
                .then((res) => {
                    this.post = this.$store.state.post = res.data.data.post;
                    this.author = this.$store.state.author = res.data.data.author;
                    this.$store.state.status = true;
                })
                .finally(() => {
                    this.loading = false;
                });

            getStat('tool',this.id).then((res) => {
                this.stat = this.$store.state.stat = res.data;
            });
            postStat('tool',this.id);
        }
    },
    components : {
        singlebox,
    }
};
</script>

